import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
	{
		path: '',
		loadChildren: () => import('./pages/my-enquiry-list/my-enquiry-list.page.module').then((m) => m.MyEnquiryListPageModule),
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class MyEnquiriesRoutingModule {}
